export default {
    components: { InboxOutlined },
    props: {
        dataname: String,
        description: String,
        screenshot: String,
        wrap: Function,
    },
    setup(props: any) {
        const store = useStore();
        const state = reactive({
            rawData: "",
            convertedData: "",
        });
        let { wrap } = props;
        const handleChange = (info: UploadChangeParam) => {
            const status = info.file.status;
            if (status !== "uploading") {

                console.log(info.file, info.fileList);
                let reader = new FileReader();
                reader.onload = (e: any) => {
                    let text = e.target.result;
                    state.rawData = text;
                };
                reader.readAsText(info.file.originFileObj as any);
            }
            if (status === "done") {
                message.success(`${info.file.name} file uploaded successfully.`);
            } else if (status === "error") {
                message.error(`${info.file.name} file upload failed.`);
            }
        };

        const download = () => {
            var blob = new Blob([state.convertedData], {
                type: "text/plain;charset=utf-8",
            });

            saveAs(blob, "transformed.csv");
        };

        watch(
            () => state.rawData,
            (text) => {
                let data = wrap(text);
                console.log(data);

                state.convertedData = Papa.unparse(data);
            }
        );

        onMounted(() => {
            if (props.dataname === "multiTimeline") {
                state.rawData = sampleStrMulti;
            } else if (props.dataname === "geoMap") {
                state.rawData = sampleStrGeo;
            } else if (props.dataname === "geoMap (single term)") {
                state.rawData = sampleStrGeoSingle;
            } else if (props.dataname === "relatedQueries") {
                state.rawData = sampleStrRQ;
            }
        });

        return {
            ...toRefs(state),
            handleChange,
            fileList: ref([]),
            handleDrop: (e: DragEvent) => {
                console.log(e);
            },
            download,
        };
    },
};
