
import "bootstrap/dist/css/bootstrap.css";

import { defineComponent } from "vue";
import { useStore } from "@/store/index";

import UserPanel from "./UserPanel/UserPanel.vue";
import ReadMe from "./ReadMe/ReadMe.vue";
import DataWrapper from "./DataWrapper/DataWrapper.vue";

import { onMounted, computed, reactive, toRefs, h, watch } from "vue";
import * as Papa from "papaparse";

const wrapmulti = (text: string) => {
    var lines = text.split("\n");
    let category = lines[0].split("Category:")[1].trim();

    var data = Papa.parse(lines.slice(2).join("\n")).data as any[];

    var headers = data.shift() as string[];
    let region = headers[1].split(":")[1].replace(/[()]/g, "").trim();
    headers = headers.map((v, idx) => {
        return idx === 0 ? v : v.split(":")[0];
    });

    var results = [] as any[];
    data.forEach((x: string[]) => {
        let datetime = x.shift();

        x.forEach((v, idx) => {
            results.push({
                [headers[0]]: datetime,
                Term: headers[idx + 1],
                GoogleIndex: v === "<1" ? 0 : parseInt(v),
                Category: category,
                Location: region,
            });
        });
    });

    return results;
};

const wrapGeo = (text: string) => {
    var lines = text.split("\n");
    console.log(lines);
    let category = lines[0].split("Category:")[1].trim();

