const wrapRelatedQuery = (text: string) => {
    console.log(text.split("\n"));
    let halves = text.split("RISING\n");

    let firsthalf = halves[0];
    let secondhalf = halves[1];

    var lines = text.split("\n");
    let category = lines[0].split("Category:")[1].trim();
    let line2 = lines[1].replace('"', ''); //"Apple: (20/03/2022 - 20/03/2023, United States)"
    let term = line2.split(":")[0];
    let others = line2.split(":")[1].replace(/[()]/g, "").trim();
    let datesrange = others.split(",")[0].trim();
    let location = others.split(",")[1].replace('"', '').trim();
    let timestart = datesrange.split("-")[0].trim();
    let timeend = datesrange.split("-")[1].trim();

    var results = [] as any[];
    var data = Papa.parse(firsthalf.split("TOP\n")[1]).data as any[];
    data.forEach((x: string[]) => {
        let relatedQuery = x[0];
        let GoogleIndexOfRelatedQuery = x[1];

        if (relatedQuery.length ===0) return;

        results.push({
            Category: category,
            TimeStart: timestart,
            TimeEnd: timeend,
            OriginalQuery: term,
            Location: location,
            RelatedQuery: relatedQuery,
            GoogleIndexOfRelatedQuery,
            Type: 'Top'
        });
    });

    return results;
};

const datawrapper = [
