export default defineComponent({
    name: "App",
    components: { UserPanel, ReadMe, DataWrapper },
    setup() {
        const store = useStore();

        const state = reactive({});

        // Init the store to read data from backend
        onMounted(async () => {
            await store.dispatch("init");
        });

        return {
            ...toRefs(state),
            datawrapper,
        };
    },
    computed: {},
});
